import React from 'react';
import { connect } from 'react-redux';
import ProductPositionDetailsComponent from './ProductPositionDetailsComponent';
import DepositReferenceIdModalContainer from '../Retail/RetailSidePanes/RetailFiatDeposit/DepositReferenceIdModalContainer';
import {
  fetchingSelector,
  productPositionAssetsSelector
} from '../../redux/selectors/productPositionSelectors';
import {
  openDepositSidePane,
  openWithdrawSidePane
} from '../../redux/actions/sidePaneActions';

const mapStateToProps = state => {
  const {
    position,
    i18nState: { translations }
  } = state;
  const fetching = fetchingSelector(state);
  let positionDetails = productPositionAssetsSelector(state).find(
    i => i.ProductId === position.selectedProductId
  );
  const { userInfo } = state.user;

  return {
    user: userInfo,
    positionDetails,
    translations,
    fetching,
    DepositReferenceIdModalComponent: <DepositReferenceIdModalContainer />
  };
};

const mapDispatchToProps = {
  openDepositSidePane,
  openWithdrawSidePane
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductPositionDetailsComponent);



