import React from 'react';
import PropTypes from 'prop-types';
import APIcon from '../common/APIcon/APIcon';
import Spinner from '../common/Spinner/Spinner';
import APButton from '../common/APButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { styleNames } from '../../propTypes/commonComponent';

import './BalancesButtonsComponent.css';

const balanceHeaderClasses = getBEMClasses('balances-buttons');

class BalancesButtonsComponent extends React.Component {
  static contextTypes = {
    t: PropTypes.func.isRequired
  };

  static propTypes = {
    openDepositSidePane: PropTypes.func.isRequired,
    openWithdrawSidePane: PropTypes.func.isRequired,
    isDepositDisabled: PropTypes.bool,
    isWithdrawDisabled: PropTypes.bool,
    showNotificationForWithdraw: PropTypes.func,
    showNotificationForDeposit: PropTypes.func
  };

  constructor(props) {
    super(props);
    this.state = {};
  }

  renderDepositWithdrawButtons() {
    return (
      <React.Fragment>
        <APButton
          onClick={
            this.props.isDepositDisabled
              ? this.props.showNotificationForDeposit
              : this.props.openDepositSidePane
          }
          classModifiers={this.props.isDepositDisabled ? 'disabled' : null}
          styleName={styleNames.additive}
          customClass={balanceHeaderClasses('deposit')}
          data-test="Deposit">
          <APIcon
            name="deposit"
            customClass={balanceHeaderClasses('button-img')}
            classModifiers="small"
          />
          {this.context.t('Deposit')}
        </APButton>

        <APButton
          classModifiers={this.props.isWithdrawDisabled ? 'disabled' : null}
          onClick={
            this.props.isWithdrawDisabled
              ? this.props.showNotificationForWithdraw
              : this.props.openWithdrawSidePane
          }
          styleName={styleNames.subtractive}
          customClass={balanceHeaderClasses('withdraw')}
          data-test="Withdraw">
          <APIcon
            name="withdraw"
            customClass={balanceHeaderClasses('button-img')}
            classModifiers="small"
          />
          {this.context.t('Withdraw')}
        </APButton>
      </React.Fragment>
    );
  }

  render() {
    return (
      <div className={balanceHeaderClasses()}>
        {this.props.user.UserName ? (
          this.renderDepositWithdrawButtons()
        ) : (
          <Spinner />
        )}
      </div>
    );
  }
}

BalancesButtonsComponent.propTypes = {
  user: PropTypes.object
};

BalancesButtonsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default BalancesButtonsComponent;



