import React from 'react';
import PropTypes from 'prop-types';
import ProductIconContainer from '../common/ProductIcon/ProductIconContainer';
import BalancesButtonsComponent from '../BalancesButtons/BalancesButtonsComponent';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import Spinner from '../common/Spinner/Spinner';

import './ProductPositionDetailsComponent.css';

const baseClasses = getBEMClasses('product-position-details');

const ProductPositionDetailsComponent = (props, context) => {
  const { positionDetails, DepositReferenceIdModalComponent } = props;

  return (
    <div className={baseClasses()}>
      {props.fetching ? (
        <Spinner isInline customClass={baseClasses} />
      ) : (
        <React.Fragment>
          <div className={baseClasses('header-icon')}>
            <div className={baseClasses('product-label')}>
              <div className={baseClasses('product-icon')}>
                <ProductIconContainer
                  iconFileName={positionDetails.iconFileName}
                  size={48}
                />
              </div>
              <div className={baseClasses('product-symbol')}>
                <div
                  className={baseClasses('product-symbol-name')}
                  style={{ color: positionDetails.productColor }}>
                  {positionDetails.Product}
                </div>
                <div className={baseClasses('product-name')}>
                  {positionDetails.ProductFullName}
                </div>
              </div>
            </div>
            <BalancesButtonsComponent
              key="1"
              isDepositDisabled={props.isDepositDisabled}
              isWithdrawDisabled={props.isWithdrawDisabled}
              user={props.user}
              openDepositSidePane={() =>
                props.openDepositSidePane(positionDetails)
              }
              openWithdrawSidePane={() =>
                props.openWithdrawSidePane(positionDetails)
              }
              showNotificationForDeposit={props.showNotificationForDeposit}
              showNotificationForWithdraw={props.showNotificationForWithdraw}
            />
          </div>
          <div className={baseClasses('header-details')}>
            <div className={baseClasses('detail')}>
              <div className={baseClasses('label')}>
                {context.t('Available Balance')}
              </div>
              <div
                className={baseClasses('value')}
                data-test="Available Balance">
                {positionDetails.AvailableBalance}
              </div>
            </div>
            <div className={baseClasses('detail')}>
              <div className={baseClasses('label')}>{context.t('Hold')}</div>
              <div className={baseClasses('value')} data-test="Hold">
                {positionDetails.Hold}
              </div>
            </div>
            <div className={baseClasses('detail')}>
              <div className={baseClasses('label')}>
                {context.t('Pending Deposits')}
              </div>
              <div
                className={baseClasses('value')}
                data-test="Pending Deposits">
                {positionDetails.PendingDeposits}
              </div>
            </div>
            <div className={baseClasses('detail')}>
              <div className={baseClasses('label')}>
                {context.t('Total Balance')}
              </div>
              <div className={baseClasses('value')} data-test="Total Balance">
                {positionDetails.Amount}
              </div>
            </div>
          </div>
          {DepositReferenceIdModalComponent}
        </React.Fragment>
      )}
    </div>
  );
};

ProductPositionDetailsComponent.propTypes = {
  positionDetails: PropTypes.object,
  fetching: PropTypes.bool.isRequired,
  user: PropTypes.object.isRequired,
  openDepositSidePane: PropTypes.func.isRequired,
  openWithdrawSidePane: PropTypes.func.isRequired,
  isDepositDisabled: PropTypes.bool,
  isWithdrawDisabled: PropTypes.bool,
  showNotificationForDeposit: PropTypes.func,
  showNotificationForWithdraw: PropTypes.func,
  DepositReferenceIdModalComponent: PropTypes.element
};

ProductPositionDetailsComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ProductPositionDetailsComponent;



