import { connect } from 'react-redux';
import DepositReferenceIdModal from './DepositReferenceIdModal';
import modalDecorator from '../../../../hocs/modalDecorator';
import { MODAL_TYPES } from '../../../../redux/actions/modalActions';

const mapStateToProps = state => {
  const { requestcode } = state.deposit.depositStatus;

  return {
    referenceCode: requestcode
  };
};

const container = connect(
  mapStateToProps,
  null
)(DepositReferenceIdModal);

export default modalDecorator({
  name: MODAL_TYPES.DEPOSIT_REFERENCE_ID
})(container);



